
div {
  box-sizing: border-box;
}

.label {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  background-color: white;
  font-size: 15px;
  font-family: Arial;
  width: 15em;
  height: 15em;
  text-align: center;
  margin: 2px;
}

.top {
  background-color: black;
  color: white;
  height: 25%;
  font-weight: bold;
}

.price {
  font-size: 2.2em;
  height: 65%;
  padding: 0.1em 0;
}

.points {
  font-size: 0.8em;
  height: 35%;
  padding-top: 0.2em;
}

[class$="-bottom"] {
  height: 75%;
}

.left-bottom {
  float: left;
  width: 40%;
  padding: .2em 0;
}

.desc {
  height: 90%;
  line-height: 1.3;
  overflow: hidden;
  word-break: break-all;
}

.size {
  height: 10%;
}

.right-bottom {
  float: right;
  width: 60%;
  padding-top: .2em;
  font-size: .8em;
  font-weight: 700;
}

.m-code {
  overflow: hidden;
  height: 10%;
}

.code {
  height: 10%;
}

.qr-code {
  height: 80%;
  padding: 0.2em 0;
}

.qr {
  width: 120px;
  height: 120px;
  margin: auto;
}


@media print {
  body * {
    visibility: hidden;
  }
  header,footer {
    display: none;
  }

  #section-to-print, #section-to-print * {
    visibility: visible;
  }

  #section-to-print {
    position: absolute;
    left: 0;
    top: 0;
  }

  .label {
    display: block;
    margin: 0;
    -webkit-print-color-adjust: exact;
  }
}
